import Bmob from "hydrogen-js-sdk";

import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import uniIcon from '@/components/uni-icon/uni-icon.vue'
import loading from "@/components/Loading/index.vue"
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"

let that;
let search_text;
let uid;
export default {
	components: {
		uniSegmentedControl,
		loading,
		uniNavBar,
		faIcon,
		uniIcon
	},
	data() {
		return {
			loading: true,
			staffs: null,
			is_choose: false,
			items: ['已启用', '未启用'],
			current: 0,
			disabled: false
		}
	},

	onLoad(options) {
		that = this;
		uni.removeStorageSync("")
		uid = uni.getStorageSync('uid')
		console.log(options)
		if (options.type == "choose") {
			that.is_choose = true
		}
	},
	onShow() {
		uni.removeStorageSync("staff")
		that.getstaff_list()
	},
	onUnload() {
		search_text = ""
	},
	methods: {
		
		//去到员工详情
		goto_detail(staff){
			uni.setStorageSync("staff", staff)
			uni.navigateTo({
				url: "detail/detail"
			})
		},

		//tab点击
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index

				if (index == 0) {
					that.disabled = false,
						that.getstaff_list()
				} else if (index == 1) {
					that.disabled = true,
						that.getstaff_list()
				}
			}
		},

		//选择此员工当负责人
		select_this(charge) {
			uni.setStorageSync("charge", charge)
			uni.navigateBack({
				delta: 1
			})
		},

		//前去添加员工
		goto_add() {
			let user = uni.getStorageSync("user")
			let identity = uni.getStorageSync("identity")
			if(user.is_vip || that.staffs.length <2){
				uni.navigateTo({
					url: "add/add"
				})
			}else{
				uni.showModal({
				    title: '提示',
				    content: '非会员最多上传2个员工账号',
						confirmText:"充值会员",
				    success: function (res) {
				        if (res.confirm) {
									if(identity == 1){
										uni.navigateTo({
											url:"/pages/mine/vip/vip"
										})
									}else{
										uni.showToast({
											title:"员工不能充值",
											icon:"none"
										})
									}
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				})
			}
			
		},

		//输入内容筛选
		input_confirm(e) {
			search_text = e.detail.value
			that.getstaff_list();
		},

		//得到员工列表
		getstaff_list: function() {
			const query = Bmob.Query("_User");
			query.order("-createdAt");
			query.equalTo("masterId", "==", uid);
			if(that.disabled){
				query.equalTo("disabled", "==", that.disabled);
			}
			query.include("shop")
			if (search_text) {
				query.equalTo("username", "==", {
					"$regex": "" + search_text + ".*"
				});
			}
			query.find().then(res => {
				//console.log(res)
				that.loading = false;
				that.staffs = res;
			});
		},

	}
}
